export const clReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CREATE_CL":
      return {
        ...state,
        state: action.payload,
      };
    case "GET_CL":
      return {
        ...state,
        state: action.payload,
      };
    case "RESET_CL":
      return {
        ...state,
        state: initialState.state,
      };
      break;

    default:
      return state;
      break;
  }
};
